<script>
import Fetch from '../../utils/fetch'
import Fetch2 from '../../utils/fetch2'
import Fetch3 from '../../utils/fetch3'
import { Message } from 'element-ui'

export default {
  data() {
    return {
      myHeaders: { Authorization: 'Bearer ' + localStorage.getItem('pstt') },
      config: {
        status: {},
      },
      form: {
        onepay_save_is_open: '',
        onepay_transfer_is_open: '',
        bank_name: '',
        wx_is_open: '',
        alipay_code: [],
        wx_code: '',
        alipay_is_open: '',
        bank_user: '',
        bank_card: '',
        bank_is_open: '',
        invest_min_money: 0,
        cost_min_money: 0,
        alipay_content: '',
        wx_content: '',
        huzhan_min_money: 0,
        bank_back_apr: 0,
        bank_is_back: 'N',
		trc_address:null,
		trc_code:null,
		erc_address:null,
		erc_code:null,
		rate:null,
		usdt_open:''
      },
      file1: [],
      file2: [],
      fileList: [],
      titleFileList: [],
      loading: false,
	  trc_code:[],
	  erc_code:[],
    }
  },
  computed: {},
  mounted() {
    this.loading = true
    Fetch2('/sys/config/infopay')
      .then(r => {
        this.loading = false
        for (const key in this.form) {
          if (this.form.hasOwnProperty(key)) {
            const item = r.data[key]
            this.form[key] = item
            this.file2 = r.data.wx_code
              ? [
                  {
                    name: r.data.wx_code.split('/').pop(),
                    url: r.data.wx_code,
                  },
                ]
              : []
            this.file1 = r.data.alipay_code
              ? [
                  {
                    name: r.data.alipay_code.split('/').pop(),
                    url: r.data.alipay_code,
                  },
                ]
              : []
          }
		  if (r.data.trc_code) {
		  	this.trc_code = [
		  		{
		  			url: r.data.trc_code,
		  			name: r.data.trc_code,
		  		},
		  	]
		  }
		  if (r.data.erc_code) {
		  	this.erc_code = [
		  		{
		  			url: r.data.erc_code,
		  			name: r.data.erc_code,
		  		},
		  	]
		  }
        }
      })
      .catch(() => (this.loading = false))
  },
  methods: {
    fileSuccess1(res, file) {
      this.file1 = [file]
      this.form.alipay_code = res.data.file
    },
    fileSuccess2(res, file) {
      this.file2 = [file]
      this.form.wx_code = res.data.file
    },
    fileSuccessBefore(file) {
      const uploadLimit = 2
      const filetype = file.name.replace(/.+\./, '')
      const uploadTypes = ['png', 'jpg', 'jpeg', 'gif']
      const isRightSize = (file.size || 0) / 1024 / 1024 < uploadLimit
      if (!isRightSize) {
        this.$message.error('文件大小超过 ' + uploadLimit + 'MB')
        return false
      }
      if (uploadTypes.indexOf(filetype.toLowerCase()) === -1) {
        this.$message.warning({
          message: '请上传后缀名为png、jpg、jpeg、gif的附件',
        })
        return false
      }
      return true
    },
	trcSuccess(res, file) {
	  this.trc_code = [file]
	  console.log(res.data.file)
	  this.form.trc_code = res.data.file
	},
	trcSuccessBefore(file) {
	  const uploadLimit = 2
	  const filetype = file.name.replace(/.+\./, '')
	  const uploadTypes = ['png', 'jpg', 'jpeg', 'gif']
	  const isRightSize = (file.size || 0) / 1024 / 1024 < uploadLimit
	  if (!isRightSize) {
	    this.$message.error('文件大小超过 ' + uploadLimit + 'MB')
	    return false
	  }
	  if (uploadTypes.indexOf(filetype.toLowerCase()) === -1) {
	    this.$message.warning({
	      message: '请上传后缀名为png、jpg、jpeg、gif的附件',
	    })
	    return false
	  }
	  return true
	},
	ercSuccess(res, file) {
	  this.erc_code = [file]
	  console.log(res.data.file)
	  this.form.erc_code = res.data.file
	},
	ercSuccessBefore(file) {
	  const uploadLimit = 2
	  const filetype = file.name.replace(/.+\./, '')
	  const uploadTypes = ['png', 'jpg', 'jpeg', 'gif']
	  const isRightSize = (file.size || 0) / 1024 / 1024 < uploadLimit
	  if (!isRightSize) {
	    this.$message.error('文件大小超过 ' + uploadLimit + 'MB')
	    return false
	  }
	  if (uploadTypes.indexOf(filetype.toLowerCase()) === -1) {
	    this.$message.warning({
	      message: '请上传后缀名为png、jpg、jpeg、gif的附件',
	    })
	    return false
	  }
	  return true
	},
    handleImageAdded(file, Editor, cursorLocation, resetUploader) {
      const formData = new FormData()
      formData.append('file', file)
      Fetch('/api/api/upload', formData).then(result => {
        let url = result.data.file
        Editor.insertEmbed(cursorLocation, 'image', url)
        resetUploader()
      })
    },
    onSubmit() {
      Fetch3('/sys/config/infopay', {
        ...this.form,
      })
        .then(() => {
          this.loading = false
          Message.success('操作成功')
        })
        .catch(() => {
          Message.error('操作失败')
          this.loading = false
        })
    },
  },
}
</script>
